import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Landing.css';

const LinkCard = props => {
  return (
    <Link className='Card' to={props.path}>
      <h3 className='ButtonCard__Label'>{props.label}</h3>
      <div className='ButtonCardContainer'>
        <span className={`ButtonCardContainer-Icon ${props.icon}`}></span>
      </div>
    </Link>
  );
};

const VideoCard = props => {
  return (
    <a href='/channel/1' className='Card'>
      <div className='Card__Label'>
        Channel
      </div>
    </a>
  );
};

export default class Landing extends Component {
  render() {
    const myArr = new Array(3);
    const popArr = new Array(5);
    myArr.fill(1);
    popArr.fill(1);
    return (
      <div className='LandingContainer'>
        <h2>My Channels</h2>
        <div className='Carousel'>
          <LinkCard
            path='/createchannel'
            label='Create Channel'
            icon='ion-md-add-circle-outline'/>
          {myArr.map(() => <VideoCard/>)}
          <LinkCard
            path='/channels'
            label='View All'
            icon='ion-md-arrow-forward'/>
        </div>
        <h2>Popular Channels</h2>
        <div className='Carousel'>
          {popArr.map(() => <VideoCard/>)}
        </div>
        <h2>Favorites</h2>
        <div className='Carousel'>
          {myArr.map(() => <VideoCard/>)}
          <LinkCard
            path='/channels'
            label='View All'
            icon='ion-md-arrow-forward'/>
        </div>
      </div>
    );
  }
}
